import React from 'react';
import { useRef } from 'react';
import { Network, NETWORKS } from '../../networks';
import './networkButton.css';

interface Props {
    network: Network | null;
    setOpenNetwork: React.Dispatch<React.SetStateAction<boolean>>;
    setSideMenu: React.Dispatch<React.SetStateAction<boolean>>;
}

interface SwitchEthereumChainParameter {
    chainId: string;
}

interface AddEthereumChainParameter {
    chainId: string; // A 0x-prefixed hexadecimal string
    chainName: string;
    nativeCurrency: {
        name: string;
        symbol: string; // 2-6 characters long
        decimals: 18;
    };
    rpcUrls: string[];
    blockExplorerUrls?: string[];
    iconUrls?: string[]; // Currently ignored.
}

type errorType = {
    code: number;
};

const NetworkButton: React.FC<Props> = (props: Props) => {
    const setOpenNetwork = useRef<React.Dispatch<React.SetStateAction<boolean>> | null>(null);
    setOpenNetwork.current = props.setOpenNetwork;

    // const handleOpenNetworks = () :void => {
    // 	if(setOpenNetwork.current){
    // 		setOpenNetwork.current(true)
    // 		props.setSideMenu(true)
    // 	}
    // }

    const handleSwitchNetwork = () => {
        switchNetwork(NETWORKS['0x4e454153']);
    };

    const switchNetwork = async (item: Network): Promise<void> => {
        try {
            const net: SwitchEthereumChainParameter = { chainId: item.chainId };
            if (window.ethereum) {
                await window.ethereum.request({
                    method: 'wallet_switchEthereumChain',
                    params: [net],
                });
            }
        } catch (error) {
            if ((error as errorType).code !== 4001) handleNetworkClick(item);
        }
    };

    const handleNetworkClick = async (item: Network): Promise<void> => {
        if (item.chainId === '0x1' || item.chainId === '0x2a') {
            return;
        }

        const net: AddEthereumChainParameter = {
            chainId: item.chainId,
            chainName: item.network,
            nativeCurrency: {
                name: item.symbol,
                symbol: item.symbol,
                decimals: 18,
            },
            rpcUrls: item.rpcUrls ? item.rpcUrls : [],
            blockExplorerUrls: item.blockExplorerUrls ? item.blockExplorerUrls : [],
        };

        if (window.ethereum) {
            try {
                await window.ethereum.request({
                    method: 'wallet_addEthereumChain',
                    params: [net],
                });
            } catch (err) {
                console.log(err);
            }
        }
    };

    if (props.network) {
        return (
            // <div className="network-button" onClick={() => handleOpenNetworks()}>
            <div className="network-button">
                {
                    <div className="network-button-content">
                        <img src={props.network.logo} alt="" className="network-logo" />
                        <span className="network-name">{props.network.network}</span>
                        {/* <span className="arrow">&#9660;</span>     */}
                    </div>
                }
            </div>
        );
    } else
        return (
            // <div className="network-button" onClick={() => handleOpenNetworks()}>
            <div className="network-button" onClick={() => handleSwitchNetwork()}>
                <div className="network-button-content">
                    <span className="network-name" style={{ color: 'red' }}>
                        Wrong Network
                    </span>
                    {/* <span className="arrow">&#9660;</span>     */}
                </div>
            </div>
        );
};

export default NetworkButton;
